import React from 'react';
import { Grid, Typography } from '@mui/material';

import hero from '../../assets/hero.png';

import './home.css';

const Home = () => {
  return (
      <section id="home" className="container">
        <Grid container>
            <Grid className="full-height" item xs={12} md={6}>
                <div className="hero-caption">
                  <Typography variant="h1" className="title">
                    Hi! I am Maitreyee
                  </Typography>
                  <Typography className="text">
                    Here is some placeholder text to stretch the content to more lines. Here is some placeholder text to stretch the content to more lines. Here is some placeholder text to stretch the content to more lines. Welcome to my site!
                  </Typography>
                </div>
            </Grid>
            <Grid className="full-height" item xs={12} md={6}>
                <img className="hero-img" src={hero} alt="Yogmitali Landing Page"/>
            </Grid>
        </Grid>
      </section>
  )
}

export default Home