import React from 'react'
import { Grid, Typography } from '@mui/material'

import about1 from '../../assets/about/first_para.png'
import about2 from '../../assets/about/second_para.png'
import about3 from '../../assets/about/third_para.png'
import tl1 from '../../assets/about/timeline_1.png'
import tl2 from '../../assets/about/timeline_2.png'
import tl3 from '../../assets/about/timeline_3.png'

import './about.css'


const About = () => {
  return (
    <section id="about">
      <div className="container">
        <Grid container className="grid-container">
          <Grid item xs={12} md={5} className="left-grid">
            <img src={about1} alt="Mrs. Maitreyee Sharma, instructor and owner of YogaMitali" />
          </Grid>
          <Grid item xs={0} md={2}>
            <div className="middle-line"></div>
            <img src={tl1} alt="Timeline icon 1" className="tl-icon" />
          </Grid>
          <Grid item xs={12} md={5} className="right-grid">
            <Typography className="about-text">
                YogaMitali is a private Yoga Centre which was formed in 2019 and which runs around the Globe. It is basically an Online Platform providing general Yoga classes in Groups, Individual, Therapy Classes to almost all age groups.
            </Typography>
            <Typography className="about-text">
                The owner and the Instructor of YogaMitali is Mrs. Maitreyee Sharma, a Certified Yoga Instructor from a renowned  Institute S-Vyasa, Bengaluru. She is certified Pre and Post Natal Yoga Instructor. She is presently in her final semester of M.Sc. Yoga from Tamil Nadu Physical Education and Sports University. She is also a certified Yoga Therapist under the Indian Yoga Association and Asian Yoga Therapy Association.
            </Typography>
          </Grid>
        </Grid>
        <Grid container className="grid-container">
          <Grid item xs={12} md={5} className="left-grid">
            <Typography className="about-text">
              Since the past 3 years, YogaMitali has been a friend to innumerable suffering Souls. Maitreyee has worked with people suffering from different variety of ailments - Alzheimers, Foot Drop, Infertility issues, PCOD, Gastro Intestinal problems etc. Her major area of work is with Women and Elderly population.
            </Typography>
          </Grid>
          <Grid item xs={0} md={2}>
            <div className="middle-line"></div>
            <img src={tl2} alt="Timeline icon 2" className="tl-icon" />
          </Grid>
          <Grid item xs={12} md={5} className="right-grid">
            <img src={about2} alt="Classes in Canada, New Zealand and Australia" />
          </Grid>
        </Grid>
        <Grid container className="grid-container">
          <Grid item xs={12} md={5} className="left-grid">
            <img src={about3} alt="Classes in Canada, New Zealand and Australia" />
          </Grid>
          <Grid item xs={0} md={2}>
            <div className="middle-line"></div>
            <img src={tl3} alt="Timeline icon 3" className="tl-icon" />
          </Grid>
          <Grid item xs={12} md={5} className="right-grid">
            <Typography className="about-text">
              Countries where YogaMitali caters to are New Zealand, Australia, Canada and India. She tries her best to follow the Classical School of Yoga. In the present world Yoga has entered into the Glamour Industry. Maitreyee continuously fights against this mis appropriation of Yoga and shall continue working towards this aim.
            </Typography>
          </Grid>
        </Grid>
      </div>
    </section>
  )
}

export default About